@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Baloo+Chettan+2&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap");
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  font-family: "Nunito";
}

body {
  display: grid;
  font-size: calc(1rem + 1vw);
  place-items: center;
  color: var(--main-gray);
}

p {
  padding: 1rem;
}

ul {
  margin-bottom: 1rem;
  padding: 1rem;
}

:root {
  /* Primary color */
  --main-yellow: #f5df4d;
  --dark-yellow: #f3d92c;
  --white-yellow: #f9ed9f;
  /* Secondary color */
  --main-gray: #939597;
  --dark-gray: #6e7072;
  --white-gray: #c1c2c3;
  /* Third color */
  --main-blue: #4d63f5;
  --dark-blue: #1835f2;
  --white-blue: #7889f7;
  /* base colors */
  --main-white: #f6f8fa;
  --modal-white: #eceef7;
  --main-dark: #1f1f1f;
  --modal-dark: #171717;
}

h1 {
  font-family: "Dancing Script";
  color: var(--main-yellow);
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

h2,
h3 {
  font-family: "Baloo Chettan 2";
  color: var(--main-blue);
  margin: 0.75rem 0;
}

h4 {
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.job-title {
  font-size: 2rem;
  color: var(--main-gray);
}

.greeting {
  font-size: 3rem;
  padding: 2rem 1rem;
}

a {
  text-decoration: none;
}

.social-media-link {
  color: var(--white-gray);
  font-size: calc(3rem + 1vw);
  transition: color 0.3s ease-in-out;
}
.social-media-link:hover {
  color: var(--dark-gray);
}

.grid-1-2 {
  display: grid;
  place-items: start center;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  margin-bottom: 2rem;
}

.text-center {
  text-align: center;
}

.btn-container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
}

.btn {
  background-color: transparent;
  border: none;
  border-radius: 1rem;
  box-shadow: 0px 2px 10px var(--main-dark);
  color: var(--dark-gray);
  cursor: pointer;
  font-family: "Baloo Chettan 2";
  font-size: calc(1.25rem + 1vw);
  font-weight: bolder;
  outline: none;
  padding: 0.5rem 1rem;
  transition: background-color 0.3s ease-in, transform 0.1s cubic-bezier(0.165, 0.84, 0.44, 1);
  min-width: 20%;
  max-width: 150%;
}

.outline-btn {
  border: thin solid var(--main-blue);
  color: var(--main-blue);
}
.outline-btn:hover {
  background-color: var(--main-blue);
  color: var(--main-white);
}
.outline-btn:active {
  background-color: var(--main-blue);
  box-shadow: 0 0 5px var(--main-dark);
  color: var(--main-white);
  transform: scale(0.95);
}

.solid-btn {
  background: var(--main-yellow);
  padding: 0.5rem 2rem;
}
.solid-btn:hover {
  background-color: var(--white-yellow);
}

.inline-list {
  list-style: none;
  padding: 1rem;
}
.inline-list li {
  display: inline-block;
  padding: 0.25rem 1rem;
}

.bullet-list {
  text-align: left;
  margin: 0 auto;
  list-style: none;
}
.bullet-list li::before {
  content: "â€¢";
  color: var(--white-yellow);
  display: inline-block;
  width: 1rem;
  margin-left: 1rem;
}

.circle-img {
  width: 80%;
  max-width: 350px;
  border-radius: 50%;
}

.square-img {
  width: 100%;
  margin: 0 auto;
  border-radius: calc(0.2rem + 0.5vw);
}

.modal {
  display: none;
  visibility: hidden;
}